.hero {
    position: relative;
    display: flex;
    height: 100vh;
    width: 100%;
    z-index: 999;
  }
  
  .canvas {
    position: relative; /* Ensure relative positioning for z-index to work */
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100vh;
    background-color: black;

    .connecting-dots {
        position: absolute; /* Position the canvas absolutely */
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        /* Add your animation styles here */
        z-index: 1; /* Set a lower z-index */
    }

    .homepagetitle {
        position: relative; /* Ensure relative positioning for z-index to work */
        z-index: 2; /* Set a higher z-index than the canvas */
        text-align: center;
       
        color: white;
        margin-top: 280px;
        /* Add any other styles you need for your title */
      
    }

    
    .homepagetitle2 {
      position: relative; /* Ensure relative positioning for z-index to work */
      z-index: 2; /* Set a higher z-index than the canvas */
      text-align: center;
     
      color: white;
      margin-top: 0px;
      /* Add any other styles you need for your title */
  }
}

 

